import { isPlatformBrowser } from "@angular/common";
import { ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from "@angular/core";
import { AbstractControl, FormBuilder, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { ModalDismissReasons, NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Gallery, GalleryItem, ImageItem, ImageSize, ThumbnailsPosition } from "ng-gallery";
import { Lightbox } from "ng-gallery/lightbox";
import { Subject, takeUntil } from "rxjs";
import { AdminService } from "src/app/admin/admin.service";
import { UserService } from "src/app/core/user/user.service";
import { environment } from "src/environments/environment";
import Swal from "sweetalert2";


@Component({
  selector: "app-ver-orden",
  templateUrl: "./ver-orden.component.html",
  styleUrls: ["./ver-orden.component.scss"],
})
export class VerOrdenComponent implements OnInit {

  @Input() tipo: any;
  @ViewChild("verOrden", { static: false }) ModalOrden: TemplateRef<any>;
  private _unsubscribeAll: Subject<any> = new Subject<any>();

  public closeResult: string;
  public modalOpen: boolean = false;

  orden: any = {};

  limit: any = environment.pagination_limit;

  Toast: any;

  loading: boolean = true;

  acepting: boolean = true;

  items: GalleryItem[];

  showIcons: any = {};

  fieldsList: any = {};

  datosEnvio:any = {};

  types:any = {};

  typesList:any = [];

  pulseras:any = [];

  constructor(
    @Inject(PLATFORM_ID) private platformId: Object,
        private router: Router, private modalService: NgbModal,
        private _formBuilder: FormBuilder,
        private _userService: UserService,
        private _changeDetectorRef: ChangeDetectorRef,
        private _adminService: AdminService, public gallery: Gallery, public lightbox: Lightbox
  ) {
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });  
  }

  ngOnInit() {

    this._adminService.userTypes$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((types: any) => {
        // Update the selected agente
        this.types = types;
        this.loading = true;
        types.forEach(element => {
          this.getFieldsByType(element.name);
        });
        this.loading = false;
        // Mark for check
        this._changeDetectorRef.markForCheck();
      });


    this._adminService.ordenSelected$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((orden: any) => {
        // Update the manilla list
        this.orden = orden;
        this.pulseras = orden?.manillasId;
        this.datosEnvio = orden?.otros?.envio || {};
        
        // console.log(this.manilla);

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  ngOnDestroy() {
    if(this.modalOpen){
      this.modalService.dismissAll();
    }
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  openModal() {
    this.modalOpen = true;
    if (isPlatformBrowser(this.platformId)) { // For SSR 
      this.modalService.open(this.ModalOrden, { 
        size: 'lg',
        ariaLabelledBy: 'modal-basic-title',
        centered: true,
        windowClass: 'ModalOrden',
        backdrop: 'static',
        keyboard: false,
        scrollable: true
      }).result.then((result) => {
        `Result ${result}`
      }, (reason) => {
        
      });
    }
    
  }

  cambiarEstadoEsperaEfectivo(estado:any) {
    this.acepting = false;
    this._adminService.actualizarPagoPulsera(this.orden._id, estado).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response: any) => {
          if(estado === 'success'){
            this.Toast.fire({
              icon: "success",
              title: "La orden ha sido actualizada y ahora esta en la lista de solicitudes.",
            });
          }else{
            this.Toast.fire({
              icon: "success",
              title: "La orden ha sido actualizada y su pago no se ha procesado.",
            });
          }
          this.getPulserasPendientePagoPaginated(0);
          this.acepting = true;
          this._changeDetectorRef.markForCheck();
        },
        (error) => {
          this.acepting = true;
          this.Toast.fire({
            icon: "error",
            title: error.error.message,
          });
          this._changeDetectorRef.markForCheck();
        }
      );
  }

  getPulserasPendientePagoPaginated(page: any) {
    this._adminService
      .getPulserasEfectivoPaginated(page, this.limit)
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe(
        (response: any) => {
          this._adminService.updatePulserasPendienteList(response);
          this.modalService.dismissAll();
        },
        (error) => {
          this.Toast.fire({
            icon: "error",
            title: error.error.message,
          });
        }
      );
  }

  getFieldsByType(type: any) {
    this._userService.getFieldsByUserTypes(type).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response: any) => {
          this.fieldsList[type] = response;
        },
        (error) => {
          console.log(error);
        }
      );
  }
}
