import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { OrdersComponent } from './orders.component';
import { OrdersListComponent } from './orders-list/orders-list.component';
import { OrdersCashComponent } from './orders-cash/orders-cash.component';
import { AdminTypesResolver, OrdenesListResolver, pendienteEfectivoListResolver } from '../admin.resolver';

const routes: Routes = [
  {
    path: '',
    component: OrdersComponent,
    children:[
      {
        path: 'list',
        component: OrdersListComponent,
        resolve:{
          ordenes: OrdenesListResolver,
        },
        data: {
          title: "Ordenes",
          breadcrumb: "Lista"
        },
      },
      {
        path: 'pendientes-pago',
        component: OrdersCashComponent,
        resolve:{
          ordenes: pendienteEfectivoListResolver,
        },
        data: {
          title: "Ordenes",
          breadcrumb: "Pendiente pago"
        },
      },
    ],
    data: {
      title: "Ordenes",
      breadcrumb: "Ordenes"
    },
    resolve: {
      types: AdminTypesResolver,
    }
    // resolve: {
    //   manillas: AdminManillasResolver
    // }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OrdersRoutes {}
